import React from 'react';
import {Typography, Box, Paper, Divider, List, ListItem, ListItemText, Button} from '@mui/material';
import { CopyBlock, dracula } from 'react-code-blocks';
import {Link as RouterLink} from "react-router-dom";

const Lecture2: React.FC = () => {
    return (
        <Paper style={{ padding: 16 }}>
            <Box my={4}>
                <Typography variant="h4" component="h1" gutterBottom>
                    Lecture 2: File I/O
                </Typography>

                {/* Introduction to File I/O */}
                <Typography variant="h5" gutterBottom>
                    Introduction to File I/O
                </Typography>
                <Typography paragraph>
                    File input and output (I/O) operations are essential for persisting data, reading configuration files, logging, and more. File I/O allows programs to interact with files on the disk, enabling data storage and retrieval beyond the program's runtime.
                </Typography>
                <Typography paragraph>
                    Common use cases for file I/O include:
                </Typography>
                <List>
                    <ListItem>
                        <ListItemText>
                            <Typography component="span" style={{ fontWeight: 'bold' }}>
                                Data Persistence:
                            </Typography>
                            Without file I/O, any data created or modified during a program's execution would be lost when the program ends. File I/O enables saving data to disk, ensuring it can be accessed and reused in future sessions.
                        </ListItemText>
                    </ListItem>
                    <ListItem>
                        <ListItemText>
                            <Typography component="span" style={{ fontWeight: 'bold' }}>
                                Configuration Management:
                            </Typography>
                            Programs often need configuration settings that can be changed without altering the code. File I/O allows reading configuration files to customize program behavior dynamically.
                        </ListItemText>
                    </ListItem>
                    <ListItem>
                        <ListItemText>
                            <Typography component="span" style={{ fontWeight: 'bold' }}>
                                Logging:
                            </Typography>
                            Keeping track of program execution, errors, and other events is crucial for debugging and monitoring. File I/O allows writing logs to files, creating a history that can be analyzed later.
                        </ListItemText>
                    </ListItem>
                    <ListItem>
                        <ListItemText>
                            <Typography component="span" style={{ fontWeight: 'bold' }}>
                                Data Processing:
                            </Typography>
                            Many applications involve processing large datasets that are impractical to handle entirely in memory. File I/O enables reading from and writing to files in chunks, making it feasible to work with large amounts of data.
                        </ListItemText>
                    </ListItem>
                </List>

                <Divider style={{ margin: '16px 0' }} />

                {/* Opening and Closing Files */}
                <Typography variant="h5" gutterBottom>
                    Opening and Closing Files
                </Typography>
                <Typography paragraph>
                    Use the <code>open()</code> function to open a file in various modes (read, write, append, etc.). Always close files using the <code>close()</code> method to free up system resources. Alternatively, use the <code>with</code> statement to automatically close files.
                </Typography>
                <CopyBlock
                    text={`# Opening a file
file = open('example.txt', 'r')
# Do something with the file
file.close()  # Closing the file

# Using with statement
with open('example.txt', 'r') as file:
    # Do something with the file
# File is automatically closed here`}
                    language="python"
                    theme={dracula}
                    codeBlock
                />

                <Divider style={{ margin: '16px 0' }} />

                {/* Reading from Files */}
                <Typography variant="h5" gutterBottom>
                    Reading from Files
                </Typography>
                <Typography paragraph>
                    Different methods can be used to read data from files, such as <code>read()</code>, <code>readlines()</code>, and iterating over file lines using a for loop. It's important to handle file exceptions properly.
                </Typography>
                <CopyBlock
                    text={`# Reading entire file content
with open('example.txt', 'r') as file:
    content = file.read()
    print(content)

# Reading file line by line
with open('example.txt', 'r') as file:
    for line in file:
        print(line.strip())

# Reading all lines into a list
with open('example.txt', 'r') as file:
    lines = file.readlines()
    print(lines)`}
                    language="python"
                    theme={dracula}
                    codeBlock
                />

                <Divider style={{ margin: '16px 0' }} />

                {/* Writing to Files */}
                <Typography variant="h5" gutterBottom>
                    Writing to Files
                </Typography>
                <Typography paragraph>
                    Use <code>write()</code> to write strings to a file and <code>writelines()</code> to write a list of strings. Understand the differences between write modes (<code>w</code> for overwrite, <code>a</code> for append).
                </Typography>
                <CopyBlock
                    text={`# Writing to a file
with open('example.txt', 'w') as file:
    file.write('Hello, world!\\n')

# Appending to a file
with open('example.txt', 'a') as file:
    file.write('Appending a new line.\\n')

# Writing multiple lines
lines = ['First line.\\n', 'Second line.\\n', 'Third line.\\n']
with open('example.txt', 'w') as file:
    file.writelines(lines)`}
                    language="python"
                    theme={dracula}
                    codeBlock
                />

                <Divider style={{ margin: '16px 0' }} />

                {/* Working with Context Managers */}
                <Typography variant="h5" gutterBottom>
                    Working with Context Managers
                </Typography>
                <Typography paragraph>
                    The <code>with</code> statement ensures that files are properly closed after their suite finishes, even if an exception is raised. This promotes better resource management and cleaner code.
                </Typography>

                <Divider style={{ margin: '16px 0' }} />

                {/* Handling Exceptions */}
                <Typography variant="h5" gutterBottom>
                    Handling Exceptions
                </Typography>
                <Typography paragraph>
                    Use try-except blocks to handle potential file I/O errors such as <code>FileNotFoundError</code>, <code>IOError</code>, and <code>EOFError</code>. Ensure robustness and reliability of file operations.
                </Typography>
                <CopyBlock
                    text={`try:
    with open('nonexistent.txt', 'r') as file:
        content = file.read()
except FileNotFoundError:
    print("File not found. Please check the file name and path.")
except IOError:
    print("An error occurred while reading the file.")`}
                    language="python"
                    theme={dracula}
                    codeBlock
                />

                <Divider style={{ margin: '16px 0' }} />

                {/* Exercises */}
                <Typography variant="h5" gutterBottom>
                    Exercises
                </Typography>
                <Typography paragraph>
                    Try the following exercises to practice file I/O:
                </Typography>
                <Typography paragraph>
                    1. Write a function to read planet data from a file and return it as a list of dictionaries.
                </Typography>
                <Typography paragraph>
                    2. Write a function to save a list of planet data to a file.
                </Typography>
                <Typography paragraph>
                    3. Implement error handling for file operations.
                </Typography>
                <Box mt={4}>
                    <Button variant="contained" color="primary" component={RouterLink} to="/week2/lecture2/questionnaire">
                        Take the Questionnaire
                    </Button>
                </Box>
            </Box>
        </Paper>
    );
};

export default Lecture2;
